const mottoEle = document.querySelector(".motto")
const motto = mottoEle.innerHTML


export default  {
    index: 0,
    before: function () {
        mottoEle.innerHTML = ""
    },
    after: function () {
        console.log('after');
        let i = 0
        let mottoLen = motto.length;
        let writeMotto = setInterval(function () {
            if (i > mottoLen) {
                clearInterval(writeMotto)
                return
            }
            mottoEle.innerHTML = motto.slice(0, i)
            i++;
        }, 200)
    }
};




