
const navEle = document.querySelector('#nav')


export function navSwitch(swiper) {
    navEle.onclick = function (e) {
        let target = e.target;
        if(target.nodeName.toUpperCase() === 'A'){
            let index = target.dataset.index;
            index = index || 1;
            swiper.slideTo(index, 500, false);
        }
    }
}


