import  Swiper from '../lib/swiper/swiper.min.js'
import  '../lib/swiper/swiper.min.css'


function pagesMethod(pages, swiper, methodName) {
    let swiperCurrentIndex = swiper.activeIndex;
    let i = 0;
    let pagesLen = pages.length;
    for (i; i < pagesLen; i++) {
        let page = pages[i]
        if (page.index == swiperCurrentIndex) {
            if (page[methodName]) {
                page[methodName](swiper);
            }
            return
        }
    }
}

export default  function swiperInit(pages) {

    return new Swiper('.swiper-container', {
        pagination: '.swiper-pagination',
        direction: 'vertical',
        slidesPerView: 1,
        paginationClickable: true,
        mousewheelControl: true,
        onSlideChangeStart: function (swiper) {
            pagesMethod(pages, swiper, "before")
        },
        onInit: function (swiper) {
            pagesMethod(pages, swiper, "before")
            pagesMethod(pages, swiper, "after")
        },
        onSlideChangeEnd: function (swiper) {
            pagesMethod(pages, swiper, "after")
        },
    });

}


