let page5Swiper = new Swiper('.swiper-container-inset', {
    slidesPerView: 1,
    paginationClickable: true,
    autoHeight: true,
    grabCursor: true,
    scrollbar:'.swiper-scrollbar-inset',
    scrollbarHide : false,
    scrollbarDraggable : true ,
    scrollbarSnapOnRelease : true ,
    onSlideChangeEnd: function (swiper) {

    },
})
let page5ScreenshotSwiper = new Swiper('.swiper-container-inset-screenshot', {
    paginationClickable: true,

    effect : 'flip',
    flip: {
        slideShadows : true,
        limitRotation : true,
    },

    grabCursor: true,
    onSlideChangeEnd: function (swiper) {

    },
})

page5ScreenshotSwiper.params.control = page5Swiper;//需要在Swiper2初始化后，Swiper1控制Swiper2
page5Swiper.params.control = page5ScreenshotSwiper;

let fatherSwiper;


export default  {
    index: 4,
    before: function (swiper) {
        fatherSwiper = swiper
    },
    after: function (swiper) {
        fatherSwiper = swiper
    },
};




